export default {
	data() {
		return {
			stores: [],
			stopLoadShops: false,
			shopPage: 1,
			keywords:''
		}
	},
	onLoad() {
		this.getStoreList()
	},
	methods: {
		clearWords(){
			this.keywords = ''
			this.searchShop()
		},
		searchShop(){
			this.shopPage = 1
			this.stopLoadShops = false
			this.getStoreList()
		},
		toShopCenter(shop_id) {
			uni.setStorageSync("shop_id",shop_id)
			uni.navigateTo({
				url:"../storeCenter/storeCenter"
			})
		},
		getStoreList() {
			
			uni.showLoading({
				title:"加载中"
			})
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/getStoreList",
				data: {
					token: uni.getStorageSync('token'),
					keywords:this.keywords,
					page:1
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					uni.hideLoading()
					if (res.data.status == 0) {
						this.stores = res.data.data.data
						return
					}
					this.showToast(res.data.msg)
					
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		//触底加载更多所属门店
		loadMoreShops() {
			if (this.stopLoadShops) {
				return
			}
			this.shopPage++
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/getStoreList",
				data: {
					token: uni.getStorageSync('token'),
					keywords:this.keywords,
					page:this.shopPage
				},
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					if (res.data.status == 0) {
						for(let i=0;i<res.data.data.length;i++){
							this.stores.push(res.data.data[i])
						}
						return
					}
					this.stopLoadShops = true
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		}
	}
}
